import React, { Component } from "react";
import {
  Button,
  Form,
  Grid,
  Header,
  Image,
  Divider,
  Input,
  Label,
  TextArea
} from "semantic-ui-react";
import QRCode from "qrcode.react";
import bsv from "bsv";
import { getUtxosFromBitIndex, getTx, decodeTx } from "../utils/api";

var _ = {};
_.each = (a, f) => a.forEach(f);

var Signature = {
  SIGHASH_ALL: 0x01,
  SIGHASH_NONE: 0x02,
  SIGHASH_SINGLE: 0x03,
  SIGHASH_FORKID: 0x40,
  SIGHASH_ANYONECANPAY: 0x80
};

class GeneratePK extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isRequesting: false,
      properFormatPubKey: true,
      properFormatSig: true,
      value: "",
      value2: "",
      bitcoinAddress: "",
      pubKey: "",
      tx0: "",
      tx1: ""
    };
  }

  //   state = {};

  // Show label invalid public key
  displayFormatLabelPubKey() {
    if (this.state.properFormatPubKey) {
      return "";
    } else {
      return (
        <Label basic color="red" pointing="below">
          Please enter a valid public key in compressed form
        </Label>
      );
    }
  }

  // Show label invalid sig
  displayFormatLabelSig() {
    if (this.state.properFormatSig) {
      return "";
    } else {
      return (
        <Label basic color="red" pointing="below">
          Please enter a valid signature in hex
        </Label>
      );
    }
  }

  // Show QR code
  displayQRCode() {
    if (this.state.bitcoinAddress) {
      return (
        <div>
          <div className="row">
            <Divider hidden />
            <p>{this.state.bitcoinAddress}</p>
            <QRCode value={`bitcoin:${this.state.bitcoinAddress}`} size={200} />
          </div>
          <div className="row">
            <a
              className="btn btn-primary"
              target="_blank"
              href={`https://www.whatsonchain.com/address/${this.state.bitcoinAddress}`}
            >
              What's on chain!
            </a>
          </div>
          <Divider hidden />
          <Button
            color="blue"
            content={"Get Transactions"}
            onClick={this.handleGetTxButton}
            loading={this.state.isRequesting}
            size="huge"
          />
        </div>
      );
    } else {
      return "";
    }
  }

  displayJSONTXs() {
    if (this.state.tx0 && this.state.tx1) {
      return (
        <div className="row">
          <Divider hidden />
          <Label pointing="below">Funding Tx (TX0)</Label>
          <TextArea
            value={JSON.stringify(this.state.tx0, null, 2)}
            style={{ minHeight: 150, width: "550px" }}
          />
          <Label pointing="below">Unsigned Tx (TX1)</Label>
          <TextArea
            value={JSON.stringify(this.state.tx1, null, 2)}
            style={{ minHeight: 150, width: "550px" }}
          />
        </div>
      );
    } else {
      return "";
    }
  }

  handleChange = e => {
    this.setState({
      value: e.target.value
    });
  };

  handleChange2 = e => {
    this.setState({
      value2: e.target.value
    });
  };

  handleSubmit = e => {
    if (e) {
      e.preventDefault();
    }
    try {
      var pubKey = bsv.PublicKey.fromHex(this.state.value);

      this.setState({
        pubKey: pubKey,
        bitcoinAddress: pubKey.toAddress().toString(),
        properFormatPubKey: true
      });
    } catch (error) {
      this.setState({ properFormatPubKey: false });
    }
  };

  handleGetTxButton = async e => {
    if (e) {
      e.preventDefault();
    }

    this.setState({ isRequesting: true });
    let utxos = await getUtxosFromBitIndex(this.state.bitcoinAddress);
    this.setState({ isRequesting: false });

    let fundingTx = await getTx(utxos[0].txId);

    var pubKey = this.state.pubKey;

    var transaction = new bsv.Transaction()
      .from(utxos) // Feed information about what unspent outputs one can use
      // .addData("THRESHOLD SIGNATURE TRANSACTION TEST DEMO!") // Add OP_RETURN data
      .change(pubKey);

    // let window.tx = transaction;
    let tx1 = await decodeTx(transaction.toString());

    tx1 = JSON.parse(tx1);
    delete tx1.hex;

    this.setState({
      tx0: fundingTx,
      tx1: tx1
    });
  };

  render() {
    return (
      <Grid
        textAlign="center"
        style={{ height: "50vh" }}
        verticalAlign="middle"
      >
        <Grid.Column style={{ maxWidth: 675 }}>
          <Header as="h2" color="blue" textAlign="center" size="huge">
            <Image src="https://pbs.twimg.com/profile_images/1072445043569516544/5nUSQ-4_.jpg" />{" "}
            Threshold Signature Scheme
          </Header>

          <Divider></Divider>

          {this.displayFormatLabelPubKey}
          <Form onSubmit={this.handleSubmit}>
            <Input
              action={{
                content: "QR Code",
                onClick: () => this.handleSubmit()
              }}
              placeholder="Public Key in compressed format"
              style={{ justifyContent: "center", width: "535px" }}
              onChange={this.handleChange}
            />
          </Form>

          {this.displayQRCode()}

          {this.displayJSONTXs()}

          {/* <Input
            action={{
              color: "teal",
              labelPosition: "right",
              icon: "copy",
              content: "Copy"
            }}
            defaultValue="http://ww.short.url/c0opq"
          /> */}

          {/* ----------- */}
        </Grid.Column>
      </Grid>
    );
  }
}

export default GeneratePK;
